$color-black:#0c0c0c;
$color-backgroud:#f6f5f6;
$font-color: #a0a0a0;
@mixin flexCenter {
    display: flex;
    align-items: center;
    justify-content: center;
}
.Home{
    width: 100%;
    height: 100%;
    background-color: $color-backgroud;
    .banner{
        width: 100%;
        position: relative;
        height: calc(234.05px / 414px * 100% / 2);
        & > div:first-child{
            position: absolute;
            top: 0;
            z-index: 1;
        }
        > div:not(div:first-child){
            // height: calc(234.05px / 414px * 100%);
        }
    }
    .optionCar{
        height: 100px;
        width: 100%;
        background-color: white;
        display: flex;
        align-items: center;
        justify-content: space-around;
        div{
            height: 80%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-evenly;
            span{
                display: block;
                width: 50px;
                height: 50px;
                border-radius: 50%;
                display: flex;
                align-items: center;
                justify-content: center;
                font-size: 24px;
                background-color: #f2fbf7;
                color: #29ba7e;
                margin-bottom: 4px;
            }
            p:last-child{
                margin-top: 4px;
            }
        }
    }
    .groups{
        padding: 0 10px;
        & > div:first-child{
            height: 60px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            & > p{
                font-weight: bold;
                margin-left: 10px;
            }
            span{
                font-size: 14px;
            }
        }
        .groupsItem{
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            &>div{
                width: calc(100% / 2 - 5px);
                height: 90px;
                display: flex;
                background-color: white;
                align-items: center;
                justify-content: space-between;
                padding-bottom: 10px;
                margin-bottom: 10px;
                div:first-child{
                    margin-left: 10px;
                    display: flex;
                    flex-direction: column;
                    align-items: flex-start;
                    p{
                        margin-top: 10px;
                        font-size: 16px;
                    }
                    p:last-child{
                        font-size: 14px;
                        font-weight: 100;
                        margin-bottom: 20px;
                    }
                }
                div:last-child{
                    width: 65px;
                    height: 65px;
                    margin-right: 10px;
                    img{
                        width: 100%;
                    }
                }
            }
        }
    }
    .groupsNew{
        background-color: white;
        padding: 0 10px;
        .newTop{
            @include flexCenter;
            justify-content: start;
            font-weight: bold;
            font-size: 16px;
            padding-left: 10px;
            height: 60px;
        }
        > div:not(.newTop){
            > div{
                margin-bottom: 40px;
                position: relative;
                height: 96px;
                display: flex;
                justify-content: space-between;
                > div:first-child{
                    width: 130px;
                    img{
                        width: 100%;
                    }
                }
                > div:last-child{
                    margin-left: 10px;
                    margin-right: 10px;
                    flex: 1;
                    display: flex;
                    flex-direction: column;
                    > p{
                        font-weight: bold;
                    }
                    > div{
                        flex: 1;
                        margin: 0;
                        display: flex;
                        flex-direction: row;
                        align-items: flex-end;
                        justify-content: space-between;
                        color: $font-color;
                        p{
                            font-weight: 100;
                        }
                    }
                }
            }
            > div:not(:first-child){
                margin-top: 40px;
            }
            > div:not(:last-child):after{
                position: absolute;
                bottom: calc(0px - 20px);
                content: '';
                width: 100%;
                height: 1px;
                background-color: black;
            }
        }
    }
}