$color-black:#0c0c0c;
$color-backgroud:#fbfbfb;
@mixin defaultTopBar {
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: space-around
}
.topBarT{
    @include defaultTopBar();
    height: 50px;
}
.topBarF{
    @include defaultTopBar();

}
@mixin iconWH {
    width: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.backIcon{
    @include iconWH();
}
.topMap{
    @include iconWH();
}
.searInput{
    flex: 1;
    border-radius: 10px;
}